cc.Class({
    extends: cc.Component,


    properties: {
        editBoxes:[cc.EditBox],
        uiNode: cc.Node,
        disabled: false
    },

    onLoad: function(){
        for(let i =0; i <  this.editBoxes.length; i++){
            this.editBoxes[i].node.on("text-changed",this.onTextChangedHandler, this);
        }
        this.onTextChangedHandler();
    },

    onDestroy: function(){
        //this.editBox.node.off("text-changed",this.onTextChangedHandler, this);
    },


    onTextChangedHandler: function(){
        if(this.disabled){
            return;
        }
        let show = true;
        for(let i =0; i <  this.editBoxes.length; i++){
            if(this.editBoxes[i].string.length == 0){
                show = false;
                break;
            }
        }
        this.uiNode.active = show;
    }

})